}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  letter-spacing: .04em;
  background: none;
  border: none;
  border-radius: 4px;
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
