:root[data-theme="light"] {
  --when-modal-book-open-bgc: #111;
  --all-categories-menu-gray: #111;
  --scroller-color: #e0e0e0;
  --logotype-bookshelf: #000;
  --header-outline: #111;
  --black-first-color: #fff;
  --violet: #4f2ee8;
  --yellow: #eac645;
  --bgc: #fff;
  --gray: #b4afaf;
  --white: #fff;
  --bookname-color: #111;
  --hover: #4f2ee8;
}

:root[data-theme="dark"] {
  --when-modal-book-open-bgc: #111;
  --all-categories-menu-gray: #fff;
  --scroller-color: #111;
  --logotype-bookshelf: #f3f3f3;
  --header-outline: #fff;
  --black-first-color: #111;
  --violet: #4f2ee8;
  --yellow: #eac645;
  --bgc: #202024;
  --gray: #b4afaf;
  --white: #fff;
  --bookname-color: #fff;
  --hover: #eac645;
}

:root {
  --default-transition: color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out, fill .3s ease-in-out, transform .3s ease-in-out;
  --modal-signin-gray: rbga(17, 17, 17, 50);
  --modal-signin-white-light-theme: #f6f6f6;
  --modal-signin-white-dark-theme: rbga(246, 246, 246, 50);
  --black-second-color: #202024;
  --violet: #4f2ee8;
}

.header {
  z-index: 1000;
  width: 100%;
  border: 2px solid var(--header-outline);
  background: var(--bgc);
  border-radius: 0 0 8px 8px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  position: fixed;
}

.container-header {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 18px 0;
  display: flex;
}

.logo-link {
  height: 28px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-text {
  color: var(--logotype-bookshelf);
}

.icon {
  width: 24px;
  height: 24px;
}

.buttons-wrapper {
  justify-content: center;
  align-items: center;
  display: flex;
}

.burger-button {
  margin-left: 14px;
}

.burger-icon {
  justify-content: center;
  align-items: center;
  display: flex;
}

.navigation, .user-button {
  display: none;
}

input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 100px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: #fff;
  border-radius: 10px;
  margin: 0 auto;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + label {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + label:after {
  left: 39px;
  transform: translateX(-100%);
}

